//SEPARADOR

.separator{
 
    position: relative;
    padding: 0 7rem;

 
}
.custom-shape-divider-bottom-1675962337 {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
    transform: rotate(180deg);
}

.custom-shape-divider-bottom-1675962337 svg {
    position: relative;
    display: block;
    width: calc(256% + 1.3px);
    height: 101px;
}

.custom-shape-divider-bottom-1675962337 .shape-fill {
    fill: #000217;
}




//SPIN LOADER
.container-loader{
    position: fixed;
 
    z-index: 1000000;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #02040c;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

}

.loader {

    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    background: linear-gradient(0deg, rgba(2, 149, 223, 0.2) 33%, v.$azul 100%);
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
  }
  .loader::after {
    content: '';  
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 44px;
    height: 44px;
    border-radius: 50%;
    background: v.$azulOscuro;
  }
  @keyframes rotation {
    0% { transform: rotate(0deg) }
    100% { transform: rotate(360deg)}
  } 

  .loader-hidden{
   display: none;
  }