@use '../base/variables' as v;
.administracion-peliculas{
    background-color: v.$azulOscuro;
    position: relative;
    padding: 2rem 0;
    min-height: 100vh;
    h2{
        text-align: center;
    }
    .btn-crearprop{
        display: block;
        width: 100%;
        text-align: center;
        text-decoration: none;
        color: white;
       background-color: v.$azul;
       padding: 1rem 0;
    }
   
}
.grid-encabezado{
    display: grid;
    background-color: rgba(81, 81, 240, 0.572);
    grid-template-columns: repeat(10,1fr);
    text-align: center;
}

.grid-adminpeli{
    display: grid;
    grid-template-columns: repeat(10,1fr);
    text-align: center;
    background-color: rgba(0, 0, 0, 0.788);
    gap: 1rem;
}
.admin_imgpeli
{
    width: 100px;
    margin: 0 auto;
}

.container-buttons{
    display: grid;
    place-content: center;
    row-gap: 1rem;
   
}
.container-buttons .btn-actualizar{
    text-decoration: none;
    color: white;
    background-color: #b6846b;
    padding: .5rem .5rem;
}
.btn-borrar
{

    background-color: v.$grisOscuro;
    color: white;
    font-size: 18px;
    line-height: 0;
    height: 45px;
    width: 120px;
    &:Hover{
        cursor: pointer;
    }
}

.p_descripcion-scroll{
    overflow: visible;
    overflow-y: scroll;
    height: 100px;
}