@use '../base/mixins' as m;
.visto{
    padding: 5rem 3rem;
    background-color: #00020b;
}
.container-visto-title{
    display: flex;
    h2{
        font-size: 29px;
        margin: 0;
        margin-left: .5rem;
        @include m.desktop{
            font-size: 33px;
        }
    }
}

.visto-grid{ //POSEE  6 COLUMNAS CON LAS PELICULAS MAS RECIENTES
    margin: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

    @include m.desktop{
        @include m.grid(6);
    }

}

.peliculas-visto{ //GRID DE 3 FILAS DE LA PELICULA CON TITULO IMAGEN DESCRIPCION
    text-align: center;
    picture{
        img{
            margin: 0 auto;
        @include m.desktop{
            
        }
            height: 250px;
            width: 180px;
        }
    }
    &:hover{
     transform: scale(1.05);
     -webkit-box-shadow: 21px -16px 0px -11px rgba(47,31,59,1);
        -moz-box-shadow: 21px -16px 0px -11px rgba(47,31,59,1);
        box-shadow: 21px -16px 0px -11px rgba(47,31,59,1);
        transition: .5s all ease-in-out;
       
    }
    a{
        text-decoration: none;
        font-size: 16px;
    }

    @include m.desktop{
        text-align: left;
        
    }
}

