@use '../base/variables' as v;
.crear-pelicula{
    background-color: #02040c;
}
.container-form{
    position: relative;
    z-index: 10;
}
.form-peliculas{
    margin: 1rem 1rem;
    label{
        width: 100%;
        display: block;
        color: white;
        font-size: 19px;
    }
    input:not([type="submit"]){
        margin: 1rem 0;
        width: 100%;
        border-radius: 5px ;
        background-color: v.$negroClaro;
        border-color: rgb(198, 198, 198);
        color: white;
        height: 30px;
    }
    textarea{
        margin: 1rem 0;
        background-color: v.$negroClaro;
        color: white;
    }

    div{
        display: flex;
        flex-direction: column;
    }
    input[type="submit"]{
        margin-top: 1rem;
        width: 100%;
        background-color: v.$azul;
        color: white;
        padding: 1rem 0;
    }
}


fieldset {
    margin: 1rem 0;
    legend{
        color: white;
        font-size: 20px;
    }
    border-color: rgb(153, 153, 153);
}

.form-director{
    select{
        margin: 1rem 0;
        background-color: v.$negroClaro;
        border-radius: 5px;
        height: 24px;
        color: white;
        font-size: 16px;
    }
}
.input_genero
{
    height: 15px !important;
    width: 30px !important;

}
.form-genero
{
    display: flex;
    flex-direction: row !important;

    label{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: left;
    }
}

.imagen-small{
    width: 150px;
}
