@use '../base/variables' as v;
@use '../base/mixins' as m;
.peliculas{
    position: relative;
    z-index: 10;
    min-height: 100vh;background: rgb(0,0,0);
    background: linear-gradient(170deg, rgba(0,0,0,1) 0%, rgba(1,4,25,1) 100%);
  
}
.container-form
{
    
    padding: 4rem;
}

.container-buscar{
    display: flex;
    flex-direction: row;
    align-items: center;
    h2{
        margin: 0;
    }
    input[type="search"]{
        margin-left: 1rem;
             
        background-color: v.$azulOscuro;
        outline: none;
        border: none;
        border-bottom: 2px solid v.$blanco;
        color: v.$grisClaro;
        width: 80px;
        &:focus{
            width: 125px;
            transition: .3s width ease-in;
        }
    }
    
    .container-btnBuscar{
        margin-left: 1rem;
     
    }
}



.btnBuscar{
    opacity: 0;
    visibility: hidden;
    background-color: black;
    color: white;
    cursor: pointer;
}

.container-peliculas{
    @include m.telefono{
        @include m.grid(1);
    }
   @media(min-width:1300px){

    @include m.grid(3);

   }
    
    @include m.desktopXL
    {
        @include m.grid(6);
   
    }
 
}