@use '../base/mixins' as m;
.destacados{
    position: relative;
    z-index: 10;
    background-color: #02040c;
    padding: 3rem;
    width: 100%;
    
    
}
.destacados-subtitle{
    margin: 0;
    padding: 0 .5rem;
  
}
.title-destacados{
    display: flex;
    align-items: center;
    
   
  

}

.grid-destacados{
    margin: 3rem 0;
    text-align: center;
    font-size: 14px;
   @include m.desktop{
        @include m.grid(3);

        
    }
    
 
     
    @include m.desktopXL{
        @include m.grid(6); //EN TABLET
        width: 100%;
        text-align: left;
        font-size: 15px;

        
    }
}
.destacado{
    box-shadow: none;
    &:hover{
        -webkit-box-shadow: 21px -16px 0px -11px rgba(47,31,59,1);
        -moz-box-shadow: 21px -16px 0px -11px rgba(47,31,59,1);
        box-shadow: 21px -16px 0px -11px rgba(47,31,59,1);
        transform: scale(1.025);
        transition: .5s all ease-in-out;

    }
 
    
    hr{
        width: auto;
    }
   
    margin: 1rem 0;
    img{ //LE ASIGNO UN TAMAÑO A LA IMAGEN DE LAS PELICULAS DEL GRID
        width: 180px;
        height: 220px;
        margin: 0 auto; //CENTRO LA IMAGEN DENTRO DEL CONTENEDOR
       
    }

 

    a{
        font-size: 16px;
        text-decoration: none;
       
    }
  
}




