/** Media Queries **/
//POR HACER
@use '../base/variables' as v;
@mixin telefono(){
    @media(min-width: 450px){
        @content;
    }
}

@mixin tablet(){
    @media(min-width: 768px){
        @content;
    }
}

@mixin desktop(){
    @media(min-width: 1024px){
        @content;
    }
}
@mixin desktopXL(){
    @media(min-width: 1558px){
        @content;
    }
}


//MIXINS GRID POR HACER
@mixin grid($columnas){ /*CREO UN GRID REUTILIZABLE */
    display: grid;
    grid-template-columns: repeat($columnas,1fr);
    gap: 3rem;

}

@mixin escalar($tamaño,$tiempo){
    cursor: pointer;
    transform: scale($tamaño);
    transition: $tiempo transform ease-in-out;
}

.btn-rojo{
    border: 1px solid v.$azul;
    background-color: transparent;
    color: white;
    &:hover{
        transition: .5s all;
        cursor: pointer;
        background-color: v.$azul
    }
}

@mixin inputText($colorFondo,$largo,$alto){
    background-color: $colorFondo;
    width: $largo;
    height: $alto;
    border: none;
    color: white;
    border-bottom: 1px solid white;
    &:focus{
        transition: .5s width ease-in-out;
        border-bottom: 1px solid v.$azul;
        
    }
    


}