@use '../base/mixins' as m;
@use '../base/variables' as v;
.footer{
  position: relative;
    padding: .1rem 0;
    -webkit-box-shadow: 0px -3px 5px 0px rgb(0, 71, 213);
    -moz-box-shadow: 0px -3px 5px 0px rgb(0, 71, 213);
    box-shadow: 0px -3px 5px 0px rgb(0, 71, 213);
    background: linear-gradient(180deg, rgba(8,8,8,1) 0%, rgba(21,21,21,1) 100%); 
}
.footer-contenido{
    p{
      font-size: 16px;
        text-align: center;
        text-shadow: 0 0 5px #FFF, 0 0 10px #FFF, 0 0 15px #FFF, 0 0 20px #1859ff, 0 0 30px #1859ff, 0 0 40px #1859ff, 0 0 55px #1859ff, 0 0 75px #1859ff, -13px -7px 0px rgba(206,89,55,0);
        @include m.tablet{
          font-size: 21px;
        }
      span{
        animation: parpadeoLetras 2s linear infinite;
        color: v.$azul
      }
    }
}

@keyframes parpadeoLetras {  
  0%,10%,20% { opacity: 1.0; }
   50%,80% { opacity: 0.0; }
   60%,70%{opacity: 1;}
  100% { opacity: 1.0; }
}