@use '../base/mixins' as m;
.contenido{
    background-color: hsl(230, 100%, 1%);;
padding: 5rem 0;

    text-align: center;
  h2{
    margin: 2rem 0;
  }
    
}

.contenido-botones{
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    height: 100%;
    width: 100%;
    @include m.tablet{
        width: 400px;
    }
    

   
    a{
        margin: .3rem 0;
        text-decoration: none;
        padding: .8rem 1.6rem;

    }
    
    @include m.tablet{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        a{
            margin: 0 1rem;    
        }
    }
}