@use '../base/variables' as v;
@use '../base/mixins' as m;
.contacto{
    display: grid;
    position: relative;

    min-height: 50vh;
   
    background-color: #080808;
   
    .contacto-titulo{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        h2{
            font-size: 29px;
            letter-spacing: 1px;
            margin: 0;
            color: white;
            text-align: center

    
        }
        h3{
            font-size: 26px;
        }
        @include m.tablet{
            h2{
                font-size: 38px;             
        
            }
            
            h3{
                font-size: 35px;
            }
        }
    }
}



.container-contacto
{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
    @include m.desktop{
        @include m.grid(2);
        grid-template-columns: 1fr 1fr;
        place-content: center;
        
    }


}
.container-form{
   
        .form{
            input:not([type="submit"]),textarea{
                border-radius: 6px;
                background-color: #080808;
                border-color: #407cf3;
                width: 100%;
                -webkit-box-shadow: 0px 0px 42px -16px rgba(3,102,250,1);
                -moz-box-shadow: 0px 0px 42px -16px rgba(3,102,250,1);
                box-shadow: 0px 0px 42px -16px rgba(3,102,250,1);
                padding: 0 .5rem;
                color: white;
                &:focus{
                    transition: .5s outline ease-in-out;
                    outline: 1px solid #407cf3;
             
                }
   }

                display: grid;
                grid-template-columns: repeat(2,1fr);
                gap: 2rem;
                    .container-mail{
                        grid-column: 1/3;
                    
                    }
                    .container-telefono{
                        grid-column: 1/3;
                    
                    }
                    .container-form-descripcion{
                        grid-column: 1/3;
                        textarea{
                         
                            margin-top: 1rem;
                      
                        }
                    }
                    
                    input{
                    
                        height: 40px;
                        width: 100%;
               
                      
                    }
                   

                    label{
                        color: white;
                        font-size: 18px;
                    }
}}