.container-loader {
  position: fixed;
  z-index: 1000000;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #02040c;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.loader {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  background: linear-gradient(0deg, rgba(2, 149, 223, 0.2) 33%, #3636e5 100%);
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: #02040c;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.loader-hidden {
  display: none;
}

