@use '../base/variables' as v;
@use '../base/mixins' as m;



.menu{
 
    position: relative;
    z-index: 1;
}


.slider
{
display: none;

  @include m.desktopXL{
    visibility: visible;
    display: flex;
    flex-direction: row;
    height: 600px;
    width: auto;
  }


  align-items: center;
  justify-content: space-evenly;

  
    picture{
        height: 100%;
        

        img{
       

          width: 100%;
            height: 100%;
            object-fit: cover;

            transition: .5s all ease-in-out;
            
                &:active{
                 
                    width: auto;
                   
                }
            
            @include m.desktop{
                &:hover{
                    cursor: cursor;
                    width: 1000px;
                    opacity: 1;
                    transform: scale(1.1);
                    filter: contrast(130%);
                }
            }
         
          
        }
    }
  .slider-titlemovie{
    padding-right: 1rem;
  }
}