@use '../base/variables' as v;
@use '../base/mixins' as m;

.content{
    
    position: relative;
}
.content_play{
    width: 100%;
    padding: 2rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    position: absolute;
    z-index: 10;

    height: 100%;
    h1{
        margin: 0;
    }
   
    .content_img{
        border-radius: 20px;
        
            width: 250px;
    }

    .content_data{
        width: 100%;
        text-align: center;
        padding: 2rem 0rem;
            @include m.tablet{
                text-align: left;
                padding: 0 2rem;
                p{
                    font-size: 1.1rem;
                }
            }

        p{
            font-size: 1rem;
        }

        a{
            color: white;
            text-decoration: none;
        
        }
        h2{
            margin:0;
            margin-top: 1rem;

        }

        }

    @include m.tablet{
        display: flex;
        flex-direction: row;
        justify-content: left;
        width: 100%;
      
    }
           
        
   
}

.content_stream{
    padding: 1rem 0;
    text-align: center;
    background-color: v.$grisClaro2;
    width: 100%;
 
    a{
        text-decoration: none;
        font-size: 18px;
        color: white;

    }
    p{
        margin: 0;

    }
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}



.background{
   
 
    height: 800px;

    picture{
        height: 100%;
        img{
       
            height: 100%;
            object-fit:cover;
            object-position: center;
            filter: brightness(.6) blur(.5rem);

            display: block;
        }
    }
}

.content_data{
    ul{
        padding: 0;
     
        li{
            font-size: 20px;
            padding: .5rem 0rem;
        }
       
    }
   


    .container-likes{
        display: block;
    }
  
    .container-duracion{
        display: block;
        .logo_clock{
            margin: 0 auto;
            @include m.tablet{
               margin: 1px 1px;
             }
        }
    }
}





/*PARTE ACTORES Y SOCIAL MEDIA*/

.actors-media{
    min-height: 100vh;
    background-color: v.$azulOscuro;
}


.container-actor-media{
    @include m.tablet{
        display: grid;
        grid-template-columns: 1fr .2fr;
    }
    .container_actors{
      
        h2{
            text-align: center;
            @include m.tablet{
                text-align: left;
                
            }
        }
    }
  
  
}

.actors-scroll{
    display: flex;

    @include m.tablet{
        flex-direction: row;
        justify-content: space-evenly;
      
        overflow-x: scroll;
    }
    flex-direction: column;
    overflow-x: hidden;

}


.card{
 margin: 1rem 0;
        border-radius: 7px;
    background-color: v.$grisTransparente;
    color:white;

    a{
        text-decoration: none;
        .card-actor{
            font-size: 18px;
            margin: 0;
        }
       
    }
    p{
        padding: .5rem .5rem;
        
    }
    .card-personaje{
        margin: 0;
    }
    width: auto;

    @include m.tablet{
        img{
            width: 100%;
        }
        
    }
}


.container-media{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    picture{
        width: 50px;
    }
}