@use '../base/variables' as v;
@use '../base/mixins' as m;
.login{
    height: 100%;
}
.containerg-formlog{
    position: relative;
height: 100%;
display: grid;
place-items: center;
}
.container-formlog{
    position: absolute;
    z-index: 100;
   height: 500px;
   width: 400px;
    margin: 0 auto;
    background-color: v.$negroTransparente;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    picture{
        margin-bottom: 2rem;
        img{
            width: 50px;
            margin: 0 auto;
            
        }
    }
   
   
}

.form-login{
    input[type="text"], input[type="password"]{
        @include m.inputText(black,280px,40px); //SE ENCUENTRA EN MIXINS
        border-radius: 2px;
        margin-bottom: 1rem;
        outline: none;

        &:focus{
         
            width: 330px;
            border-radius: v.$azul;
            
        }
        
       
    }
    label{
        margin: 1rem 0;
        display: block;
        color: white;
        font-size: 1.1rem;
    }
    .form-login{
        margin-top: 1rem;
        text-align: center;
        input{
           padding: .6rem 1.5rem;
        }
    }
}


.fondo{
    height: 100%;
    width: 100%;

    picture{
        height: 100%;
        img{
         
            object-fit: cover;
            height: 100%;
            filter: brightness(.5);
        }
    }
}