@use '../base/variables' as v;
@use '../base/mixins' as m;

.boton-menu{
    width: 24px;
    position: absolute;
    z-index: 1000000;
    color: white;
    visibility: visible;
    left: 2%;
    top: 2%;

    @include m.desktop{
        visibility: hidden;
        
    }
}


.title{

    font-size: 35px;
    color: v.$azul;
    letter-spacing: 1px;



    @include m.desktop{
        visibility:visible;
        
    }
}

.container-nav{
   

    opacity: 0;
    visibility: visible;
    position: absolute; //EN CELULAR Y TABLET
    height: 100%;
    width: 100%;
    padding: 1rem 5rem;
    background: rgb(0,0,0);
    background: linear-gradient(173deg, rgba(0,0,0,1) 0%, rgba(1,3,23,1) 100%);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include m.desktop{
     
        height: 144px;
        visibility: visible;
        opacity: 1;
        position: relative;
   
        flex-direction: row;
      
        align-items: center;
         background-color: black;
     
    }

    a{
        text-decoration: none;
    }

    .navegation{
        .container-buscar{
            padding: 0 2rem;
        }
   
       display: flex;
       flex-direction: column;
       align-items: center;
       justify-content: space-between;
        a{
            text-align: center;
            padding: .2rem 0;
            color: v.$blanco;
         
            &:hover{
                font-size: v.$tamañoLinkGrande;
                transition: .4s all ease-in-out;
            }

            @include m.desktop{
                padding: 0 1rem;
                font-size: 16px;
                width: auto;
                
            }

            @include m.desktopXL{
                padding: 0 2rem;
                font-size: 18px;
            }
        }

      
        @include m.desktop{
            display: flex;
            flex-direction: row;
            align-items: center;
     
            
            
        }
    }


    -webkit-box-shadow: 0px 0px 8px 0px rgba(41,41,41,1);
    -moz-box-shadow: 0px 0px 8px 0px rgba(41,41,41,1);
    box-shadow: 0px 0px 8px 0px rgba(41,41,41,1);


   


}

//ANIMACION SUBRAYADO

.navegation{
a {
    position: relative;
    overflow: hidden;
    display: inline-block;
    text-decoration: none;
    color: v.$azul;
    }
    a:after {
    content: "";
    position: absolute;
    bottom: 0;
    left:100%;
    width: 95%;
    height: 2px;
    background: v.$azul;
    transition: left .8s;
    }
    a:hover:after {
    left: 1%;
    }

}

.navegation{

    .ul-peliculas{ //A M, LISTA DE PELICULAS LE PONGO DETERMINADAS CARACTERISTICAS
        cursor: pointer;
        padding: 0 2rem;
        font-size: 20px;
        margin: 0;
        position: relative;
        
        li:first-child{
            position: relative;
           
        }

        .contenedor-peliculas{ //AL CONTENEDOR DE MI LISTA DE GENEROS LE PONGO DETERMINADAS CARACTERISTICAS
            width: 100%;
            position: absolute;
             background-color: rgba(0, 0, 0, 0.415);
            display: flex;
            flex-direction: column;
            visibility: hidden;
            opacity: 0;
            transition: all .5s ease-in-out;
            margin: .5rem 0;
            overflow-y: scroll;
          
            height: 350px;
            
            li{ //A LA LISTA DE GENEROS LE PONGO DETERMINADAS CARACTERISTICAS
                font-size: 18px;
                position: relative;
                padding: .5rem 0;
        
                
            }

            @include m.desktop{
                    flex-direction: column;
                position: absolute;
                background-color: rgba(0, 0, 0, 0.664);
                
            }
            
        }
        
      
    }


       
    
}


//ELEMENTOS PARA JAVASCRIPT

//PARA HACER VISIBLE ALGO
.contVisible
{
    z-index: 100;
    visibility: visible !important; //CUANDO EL CONTENEDOR DE LISTA DE GENEROS TENGA ESTA CLASE QUE SE TOME MAS EN CUENTA
    opacity: 1 !important;

  
}

//FLECHA PELICULAS UL

.arrow,
.arrow:before{
  position: absolute;
  left: 50%;
}

.arrow{
  width: 5px;
  height: 5px;
  left:10%;
  bottom: 46%;
  margin: -20px 0 0 -20px;
    transform: rotate(-45deg);
  border-left: none;
  border-top: none;
  border-right: 2px #fff solid;
  border-bottom: 2px #fff solid;
  @include m.desktop{
    left:10%;
  }

}


//FLECHA DE UL PELICULAS
.rotar45{

    transform: rotate(45deg);
    transition: .3s transform ease-in-out;
}

//UL PELICULAS
.cambiarRelative{
    @include m.desktop{
    
        position: absolute !important;
        z-index: 10000;
  
    }
    position: relative !important;
    transition: height .5s ease-in-out;
   
}




//DE BARRA DE NAVEGACION
.cambiarFixed{
    @include m.desktop{
        transition: .5s visibility ease-in-out;
        position: fixed !important;
        height: 144px;
        top: 0;
       

    }
 
 
}

#btn-comenzar{ //BOTON COMENZAR

    text-decoration: none;
    text-align: center;
 padding: .8rem 1.6rem;
    color: white;
    border: 1px solid v.$azul;
  
    margin: 1rem auto;

    &:hover{
        transition: .5s all ease-in-out;
        background-color:v.$azul;
        border: white;
    }

}



